.movies-board {
    display: flex;
    gap: 1.5rem;
    color: white;
}

.movies {
    max-width: 100%;
    max-height: 100%;
}

.movie {
    margin-top: 1rem;
    text-align: center;
    line-height: 2rem;
}

.movie img {
    width: 200px;
    height: 250px;
    object-position: center;
}